const prove = {
    apply: 'certificate/queryApplyCertificateHistory', // 首页
    queryCertificateList: 'certificate/queryCertificateList', // 证明模板列表
    queryServiceRecord: 'certificate/queryServiceRecord', // 服务记录
    applyCertificate: 'certificate/applyCertificate', // 申请证明
    queryServiceRecordByCode: 'certificate/queryServiceRecordByCode', // 服务记录 编码查询
    queryPublicityCertificate: 'certificate/queryPublicityCertificate', // 公示list
    sendSms: 'sms/sendSms', // 验证码
    verifyCode: 'sms/verifyCode', // 校验
    report: 'certificate/report', // 举报
    reportRedirect: 'certificate/reportRedirect' // 举报页面跳转
};

export default {
    prove
};



