<template>
  <div class="prove-detailedList">
      <header>
        <div class="t">
          <div class="title period">服务时段/Service Period</div>
          <div>{{form.startDay}} 至 {{form.endDay}}</div>
        </div>
        <div class="b">
          <div class="title time">服务时长/Service Time</div>
          <div>{{realTimes | initTime}}</div>
        </div>
        <img src="../assets/images/detailed.png" alt="">
      </header>
      <div class="prdListWrap">
        <!-- 此接口没有提供分页字段 -->
        <!-- <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore"> -->
          <ul>
              <li v-for="(item, index) in list" :key="index" @click="open(item)">
                  <div class="col-sm col-sm-4 l">
                    <div>{{item.startTime | initDate}}</div>
                  </div>
                  <div class="col-sm col-sm-8 r">
                    <div class="t row">
                      <div class="col-sm col-sm-6 left">
                        <i class="iconfont icon-clock-blod"></i>
                        <span>{{item.times | initTime}}</span>
                      </div>
                      <div class="col-sm col-sm-6 right isManual-class">
                        <span>{{item.txtManual}}</span>
                      </div>
                    </div>
                    <div class="c">
                      <i class="iconfont icon-person-blod"></i>
                      <span>{{item.name}}</span>
                    </div>
                    <div class="b">
                      <i class="iconfont icon-address-blod"></i>
                      <span>{{item.orgName}}</span>
                    </div>
                  </div>
              </li>
          </ul>
        <!-- </mt-loadmore> -->
      </div>
      <div v-if="isMask">
        <div class="mask"></div>
        <div class="model">
          <div class="t row" :style="{backgroundImage: 'url('+note+')'}">
            <div class="close" @click="isMask = false">
              <i class="iconfont icon-close"></i>
            </div>
            <div class="text">
              <div class="name">公益上海</div>
              <div class="number">岗位编号：{{openForm.recruitCode}}</div>
            </div>
          </div>
          <div class="b">
            <div>服务日期：{{openForm.startTime | initDate}}</div>
            <div>服务时间：{{openForm.startTime | initHours}} - {{openForm.endTime | initHours}}</div>
            <div>服务地点：{{openForm.arriveAddress}}</div>
            <div class="time-wrap">
              <div v-for="(item, index) in openForm.validSignSlot" :key="index">
                <div class="row">
                    <div class="col-sm col-sm-6">
                      <i class="iconfont icon-sign"></i>
                      <span>签到时间</span>
                    </div>
                    <div class="col-sm col-sm-6">
                      <i class="iconfont icon-sign"></i>
                      <span>签出时间</span>
                    </div>
                </div>
                <div class="row">
                    <div class="col-sm col-sm-6 l">
                      {{item.startTime | initHours}}
                    </div>
                    <div class="col-sm col-sm-6 r">
                      {{item.endTime | initHours}}
                    </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/prove-head.png'),
            form: this.$route.query,
            pageNumber: 1,
            allLoaded: false,
            bottomText: '上拉加载更多...',
            list: [
                // {
                //     recruitCode: 'SROAy8xfuQhtb2qzoXM8Zox0QtJ2grBw',
                //     serialNumber: 'GW100011540431408750',
                //     user: '72-00001666-5',
                //     nickName: '',
                //     startTime: '1540431438000',
                //     endTime: '1540431449000',
                //     times: 0,
                //     name: '通用岗位',
                //     arriveTime: '2018-10-25 09:21:00',
                //     arriveAdress: '上海市长宁区愚园路1258号',
                //     demo: '',
                //     orgCode: 'BBA101181',
                //     orgName: '测试机构54665',
                //     txtManual: '人工补登'
                // }
            ],
            realTimes: null,
            isMask: false,
            openForm: {}
        };
    },
    created () {
        this.getList();
    },
    methods: {
        getList () {
            this.$http.post(this.$api.prove.queryServiceRecordByCode, {code: this.form.code}).then((res) => {
                if (res.data.code === 0) {
                    this.list = res.data.data.userTicketValidSignRespList;
                    this.realTimes = res.data.data.realTimes;
                }
            });
        },
        open (item) {
            this.isMask = true;
            this.openForm = item;
            // console.log(this.openForm.validSignSlot);
        }
        // loadBottom: function () {
        //     this.pageNumber += 1;
        //     alert(this.pageNumber);
        //     setTimeout(() => {
        //         this.$http.post(this.$api.prove.queryServiceRecordByCode, {code: this.form.code}).then(res => {
        //             if (res.data.code === 0) {
        //                 this.list = this.list.concat(res.data.data.records);
        //                 this.$refs.loadmore.onBottomLoaded();
        //                 if (res.data.data.records.length === 0) {
        //                     this.allLoaded = true;
        //                 }
        //             }
        //         });
        //     }, 500);
        // }
    }
};
</script>
<style lang="scss" scoped>
  .prove-detailedList {
    ul li .l {
      padding: 1.65rem 0.2rem;
    }
  }
  .isManual-class {
    text-align: right;
  }
  .time-wrap {
    max-height: 80px;
    overflow-y: auto;
  }
</style>



