<template>
    <section class="prove-community prdListWrap" :class="{'bg100': !isShow}">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <header>
          <div class="head" :style="{backgroundImage: 'url('+note+')'}">
            <div>说明 : </div>
            <div>1、本平台依照民政部标准MZ/T061-2015出具志愿服务记录证明及公益证明。</div>
            <div>2、该证明可用于各类需出示志愿服务证明或者公益证明的场合。</div>
          </div>
        </header>
        <div class="btn">
          <button class="submit-btn" @click="submit()">证明申请</button>
        </div>
        <div class="lc-tip">
          <span class="block"></span>
          <span>申请历史</span>
        </div>
        <div v-if="isShow">
            <ul>
                <li v-for="(item, index) in list" :key="index" @click="goPage(item)">
                    <div class="number">证明编号：{{item.code}}</div>
                    <div class="row">
                      <div class="col-sm col-sm-2 l">
                        <img :src="item.logo || logo" alt="">
                      </div>
                      <div class="col-sm col-sm-9 r">
                          <div>{{item.title}}</div>
                          <div class="c">申请日期：{{item.createTime}}</div>
                          <div class="c">状态：{{item.state | state}}</div>
                      </div>
                    </div>
                </li>
            </ul>
        </div>
        <div v-if="!isShow" class="error-wrap">
          <img src="../assets/images/license-error.png" alt="">
          <div class="msg">暂无数据</div>
        </div>
        <div v-if="isMask">
          <div class="mask"></div>
          <div class="model">
            <div class="t row">
              <div class="col-sm col-sm-6 l">
                  <i class="iconfont icon-jay"></i>
                  <span>提示</span>
              </div>
              <div class="col-sm col-sm-6 close r" @click="isMask = false">
                <i class="iconfont icon-close"></i>
              </div>
            </div>
            <div class="b">
              <div class="tips">{{state.text}}</div>
              <div class="button-wrap">
                <button class="submit-btn" @click="isMask = false">确定</button>
              </div>
            </div>
          </div>
        </div>
      </mt-loadmore>
    </section>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/prove-h-bg.png'),
            logo: require('@/assets/images/default-logo.png'),
            allLoaded: false,
            bottomText: '上拉加载更多...',
            list: [],
            isShow: true,
            isMask: false,
            form: {
                pageNumber: 1,
                pageSize: 10,
                personFirstAccount: null
            },
            state: {
                text: ''
            },
            un: null
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            let pfa = localStorage.getItem('personFirstAccount');
            pfa && (this.form.personFirstAccount = pfa);
            // 线上
            this.un = JSON.parse(localStorage.getItem('appUserInfo')).cookieResp.un;
            this.getHistory();
        },
        // 证明申请
        submit () {
            // 线上
            let token = localStorage.getItem('token');
            if (!token) {
                this.$mint.Toast('登录后才能申请证明');
                return;
            }
            if (!this.un) {
                this.$mint.Toast({
                    message: '请先实名认证',
                    position: 'top',
                    duration: 1000
                });
                return;
            }
            this.$router.push({name: 'proveApplyFor'});
        },
        getHistory () {
            this.$http.post(this.$api.prove.apply, this.form)
                .then(res => {
                    if (res.data.code === 0) {
                        this.list = res.data.data.records;
                    } else {
                        this.isShow = false;
                    }
                });
        },
        // 点击li
        // 10公示 20核发 30冻结 -10取消
        goPage (item) {
            // 20核发状态查看详情
            if (item.state === 20) {
                // type 10 志愿服务证明
                if (item.type === 10) {
                    this.$router.push({name: 'idealProve', query: item});
                } else {
                    this.$router.push({name: 'charityProve', query: item});
                }
                return;
            }
            if (item.state === 10) {
                this.state.text = '公示期间，请稍候...';
            }
            this.isMask = true;
            if (item.state === 30) {
                this.state.text = '冻结状态，管理员正在审核。';
            }
            if (item.state === -10) {
                this.state.text = '证明申请被取消';
            }
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.prove.apply, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>
<style lang="scss" scoped>
  .prove-community {
      position: relative;
  }
</style>



