<template>
    <section class="container-fluid">
        <router-view
          keep-alive
          transition
          transition-mode="out-in">
        </router-view>
    </section>
</template>

<script>
import axios from 'axios';
export default {
    data () {
        return {
            token: 'eyJhbGciOiJIUzI1NiJ9.eyJqdGkiOiIxMDIzMTA2Iiwic3ViIjoiXCIxMDIzMTA2fDcyLTAwMDAwMDA0LTRcIiIsImlzcyI6IuS4iua1t-W4guW_l-i2o-WFrOebiuWfuumHkeS8miIsImlhdCI6MTU1MDQ1NjM1NSwiZXhwIjoxNTUxNzUyMzU1fQ.jyEThZNUahu5ZgnKO1H6AzQ5wJH32qVEtkrwZLeVgDk',
            userNo: '72-00001666-5'
        };
    },
    created () {
        // 本地
        // axios.defaults.headers.token = this.token;
        // localStorage.setItem('personFirstAccount', this.userNo);
        // localStorage.setItem('token', this.toke);
        // 线上
        let storage = localStorage.getItem('appUserInfo');
        if (storage) {
            storage = JSON.parse(storage);
            axios.defaults.headers.token = storage.cookieResp.token;
            localStorage.setItem('token', storage.cookieResp.token);
            localStorage.setItem('personFirstAccount', storage.userNo);
        }
    }
};
</script>

<style lang="scss">
    @import './styles/app';
</style>



