<template>
  <div class="prove-detailedList">
    <!-- 此页面 暂时不用 -->
    <header>
      <div class="t">
        <div class="title period">公益时段/Service Period</div>
        <div>{{form.startDay}} 至 {{form.endDay}}</div>
      </div>
      <div class="b">
        <div class="title time">公益慈善次数</div>
        <div>{{realTimes}}次</div>
      </div>
      <img src="../assets/images/detailed.png" alt="">
    </header>
    <div style="overflow: scroll;">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <ul>
            <li v-for="(item, index) in list" :key="index" @click="open(item)">
                <div class="col-sm col-sm-4 l">
                  <div>{{item.startTime | date}}</div>
                </div>
                <div class="col-sm col-sm-8 r">
                  <div class="rmb"><span>捐款1，000元</span></div>
                  <div class="t">
                    <i class="iconfont icon-person-blod"></i>
                    <span>{{item.name}}</span>
                  </div>
                  <div class="b">
                    <i class="iconfont icon-address-blod"></i>
                    <span>{{item.orgName}}</span>
                  </div>
                </div>
            </li>
        </ul>
      </mt-loadmore>
    </div>
    <div v-if="isMask">
      <div class="mask"></div>
      <div class="model">
        <div class="t row" :style="{backgroundImage: 'url('+note+')'}">
          <div class="close" @click="isMask = false">
            <i class="iconfont icon-close"></i>
          </div>
          <div class="text">
            <div class="name">公益上海</div>
            <div class="number">岗位编号：{{openForm.orgCode}}</div>
          </div>
        </div>
        <div class="b">
          <div>捐款日期：{{openForm.startTime | date}}</div>
          <div>捐款：1000元</div>
          <div>服务地点：{{openForm.arriveAddress}}</div>
          <!-- <div>服务内容：{{openForm.arriveAddress}}</div> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/prove-head.png'),
            form: this.$route.query,
            pageNumber: 1,
            allLoaded: false, // 若为真,则 bottomMethod 不会被再次触发
            bottomText: '上拉加载更多...',
            list: [],
            realTimes: null,
            isMask: false,
            openForm: {}
        };
    },
    created () {
        this.getList();
    },
    methods: {
        getList () {
            this.$http.post(this.$api.prove.queryServiceRecordByCode, {code: this.form.code}).then((res) => {
                this.list = res.data.data.userTicketValidSignRespList;
                this.realTimes = res.data.data.realTimes;
            });
        },
        open (item) {
            this.isMask = true;
            this.openForm = item;
        },
        loadBottom: function () {
            this.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.prove.queryServiceRecordByCode, {code: this.form.code}).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.userTicketValidSignRespList);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.userTicketValidSignRespList.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>



