<template>
  <div class="prove-reportLog prove-report">
      <section class="lc-tips">
          <i class="iconfont icon-jay"></i>只接受实名举报
      </section>
      <section class="form">
        <div class="item row">
            <div class="col-sm col-sm-4 l">投诉对象</div>
            <input class="col-sm col-sm-8 r" type="text" v-model="form.reportObj" placeholder="请输入投诉对象">
        </div>
        <textarea cols="30" rows="10" v-model="form.content" placeholder="请输入投诉内容"></textarea>
        <div class="item row">
            <div class="col-sm col-sm-4 l">投诉人姓名</div>
            <input class="col-sm col-sm-8 r" type="text" v-model="form.reportName" placeholder="请输入投诉人姓名">
        </div>
        <div class="item row">
            <div class="col-sm col-sm-4 l">投诉人身份证号</div>
            <input class="col-sm col-sm-8 r" type="text" v-model="form.reportCardNo" placeholder="请输入投诉人身份证号">
        </div>
        <div class="item row">
            <div class="col-sm col-sm-4 l">投诉人手机</div>
            <input class="col-sm col-sm-8 r" type="tel" maxlength="11" v-model="form.reportMobile" placeholder="请输入投诉人手机">
        </div>
        <div class="item row">
            <div class="col-sm col-sm-4 l">验证码</div>
            <input class="col-sm col-sm-4 r" v-model="form.key" maxlength="6" type="tel" placeholder="请输入">
            <div class="col-sm col-sm-4 codebtn" :class="{'isCode': !flag}"  @click="sendCode">{{verCode}}</div>
        </div>
      </section>
      <div class="button-wrap">
        <button class="submit-btn-1" @click="submit">提 交</button>
      </div>
      <div v-if="isTips">
        <div class="mask"></div>
        <div class="model">
          <div class="t">
            <div class="close">
              <i class="iconfont icon-close"></i>
            </div>
            <i class="iconfont icon-jay"></i>
            <span>提示</span>
          </div>
          <div class="c">
              已收到你的举报信息，管理员即将根据您提供的信息进行核查
          </div>
          <div class="b btnWrap">
              <div class="l">
                  <button @click="jump">确 定</button>
              </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            flag: true,
            number: 60,
            verCode: '验证码',
            code: null,
            form: {
                code: null,
                reportObj: '',
                reportName: null,
                reportMobile: null,
                content: null,
                list: [],
                key: null
            },
            isTips: false
        };
    },
    created () {
        this.form.code = this.$route.query.code;
        this.form.list = this.$route.query.list;
    },
    methods: {
        sendCode () {
            if (!(/^(0|86|17951)?(13[0-9]|15[0-9]|166|17[3678]|18[0-9]|19[0-9]|14[57])[0-9]{8}$/.test(this.form.reportMobile))) {
                this.$mint.Toast({
                    message: '请输入正确的手机号码',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            const num = 59;
            if (this.flag) {
                let params = {
                    mobile: this.form.reportMobile,
                    signName: '志愿服务保障'
                };
                this.$http.post(this.$api.prove.sendSms, params).then((res) => {
                    if (res.data.code === 0) {
                        this.number = num;
                        var timing = setInterval(() => {
                            this.verCode = this.number + 's';
                            this.number--;
                            if (this.number < 0) {
                                window.clearInterval(timing);
                                this.verCode = '重新发送';
                                this.flag = true;
                            }
                        }, 1000);
                    } else {
                        this.flag = true;
                        this.$mint.Toast({
                            message: res.data.msg,
                            position: 'top',
                            duration: 1000
                        });
                    }
                });
            }
        },
        submit () {
            if (this.form.reportObj === null) {
                this.$mint.Toast({
                    message: '请输入投诉对象',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.content === null) {
                this.$mint.Toast({
                    message: '请输入投诉内容',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.reportName === null) {
                this.$mint.Toast({
                    message: '请输入投诉人姓名',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.reportCardNo === null) {
                this.$mint.Toast({
                    message: '请输入投诉人身份证号',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (!(/^(0|86|17951)?(13[0-9]|15[0-9]|166|17[3678]|18[0-9]|19[0-9]|14[57])[0-9]{8}$/.test(this.form.reportMobile))) {
                this.$mint.Toast({
                    message: '请输入正确的投诉人手机',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            if (this.form.key === null) {
                this.$mint.Toast({
                    message: '请输入验证码',
                    position: 'top',
                    duration: 1000
                });
                return false;
            }
            this.form.code = this.$route.query.code;
            this.form.list = this.$route.query.list;
            this.$http.post(this.$api.prove.report, this.form).then((res) => {
                if (res.data.code === 0) {
                    this.isTips = true;
                }
            });
        },
        jump () {
            this.isTips = false;
            this.$router.push({name: 'publicity'});
        }
    }
};
</script>
<style lang="scss" scoped>
  .prove-report {
    .form textarea {
      width: 91.5%;
      margin-top: 5px;;
    }
    .isCode {
      background-color: #616161!important;
    }
    .btnWrap {
      .l {
        text-align: center;
      }
    }
  }
</style>



