<template>
  <div class="prove-idealRecord prove-volunteer">
      <div class="body" :style="{backgroundImage: 'url('+note+')'}">
          <header>
              <div class="zh">{{form.title.split("").join(" ")}}</div>
              <div class="en">CERTIFICATE OF VOLUNTARY SERVICE</div>
              <div class="number">编号(No.)：{{form.code}}</div>
          </header>

          <table border="0">
              <tr>
                  <td style="text-align:center" rowspan="4">公益人信息（Information of charitarian）</td>
                  <td style="text-align:center"><span>姓名</span><span>（Name）</span></td>
                  <td>{{form.realName}}</td>
              </tr>
              <tr>
                  <td style="text-align:center"><span>公益护照号</span><span>（C&V Passport No.）</span></td>
                  <td>{{form.acceptCode}}</td>
              </tr>
              <tr>
                  <td style="text-align:center"><span>证件类型</span><span>（Type of ID）</span></td>
                  <td>{{form.cartType}}</td>
              </tr>
              <tr>
                  <td style="text-align:center"><span>证件号码</span><span>（ID No.）</span></td>
                  <td>{{form.cartNO}}</td>
              </tr>
              <tr>
                  <td style="text-align:center">公益次数/捐赠(Charity / Donation Times)</td>
                  <td colspan="2">{{form.time | initTime}}</td>
              </tr>
              <tr>
                  <td style="text-align:center">公益内容(Charity ServiceContent)</td>
                  <td colspan="2">请查看附带清单！</td>
              </tr>
              <tr>
                  <td style="text-align:center">其他需要说明的事项（Other Information）</td>
                  <td colspan="2">
                      <div>其他需要注意事项</div>
                      <div>{{form.info}}</div>
                      <div class="select">
                          <span>是否有附件?</span>
                          <div>
                              <span>
                                  <span class="radio">
                                    <i class="iconfont icon-tick" v-if="form.isAnnex === '1'"></i>
                                  </span>
                                  <span class="radio-text">是/Yes</span>
                                </span>
                                <span>
                                  <span class="radio">
                                    <i class="iconfont icon-tick" v-if="form.isAnnex === '0'"></i>
                                  </span>
                                  <span class="radio-text">否/Yes</span>
                                </span>
                          </div>
                      </div>
                      <div class="select">
                          <span>（With Attachment or Not?）</span>
                          <div>
                              <span>
                                  <span class="radio">
                                    <i class="iconfont icon-tick" v-if="form.isAnnex === '1'"></i>
                                  </span>
                                  <span class="radio-text">是/Yes</span>
                                </span>
                                <span>
                                  <span class="radio">
                                    <i class="iconfont icon-tick" v-if="form.isAnnex === '0'"></i>
                                  </span>
                                  <span class="radio-text">否/Yes</span>
                                </span>
                          </div>
                      </div>
                  </td>
              </tr>
          </table>
          <section class="base">
              <div class="row">
                  <div class="col-sm col-sm-6 l">签发单位(Signed by)：</div>
                  <div style="text-align:right" class="col-sm col-sm-6 r">{{form.issued}}</div>
              </div>
              <div class="row">
                  <div class="col-sm col-sm-2 l"></div>
                  <div style="text-align:right" class="col-sm col-sm-10 r">{{form.createTime.substring(0, 10)}}</div>
              </div>
              <div class="code">
                  <div class="row">
                        <div class="col-sm col-sm-8 l">注 : 本证书可通过扫描验证进行真伪验证 公益上海官方网站为<a :href="form.urlIssued">{{form.urlIssued}}</a></div>
                        <div style="text-align:right" class="col-sm col-sm-3 r"><img :src="form.qrCode" alt="">
                          <div style="text-align:center">
                              <div>长按识别二维码进行验证</div></div>
                          </div>
                  </div>
              </div>
        </section>
      </div>
      <div class="number">编号(No.)：{{form.code}}</div>
      <div class="head">
          <div class="title">志愿服务记录明细</div>
          <div class="m-title">志愿服务时段/Service Peroid：{{form.startDay}} 至 {{form.endDay}}</div>
          <div class="m-title">志愿服务时长/Service Time：{{form.time | initTime}}</div>
      </div>
      <div class="table">
          <table class="list-table">
                  <th>日期</th>
                  <th>时长</th>
                  <th>岗位</th>
                  <th>机构</th>
                  <tr v-for="(item, index) in list" :key="index">
                      <td>{{item.startTime | date('b')}}</td>
                      <td>{{item.times | initTime}}</td>
                      <td>{{item.name}}</td>
                      <td>{{item.orgName}}</td>
                  </tr>
              </table>
      </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/idealRecord-bg.png'),
            isClick: true,
            form: {},
            list: []
        };
    },
    created () {
        this.form = this.$route.query;
        this.getList();
    },
    methods: {
        getList () {
            this.$http.post(this.$api.prove.queryServiceRecordByCode, {code: this.form.code}).then((res) => {
                this.list = res.data.data.userTicketValidSignRespList;
                console.log(this.list);
            });
        }
    }
};
</script>
<style lang="scss" scoped>
  table {
    border-collapse: collapse;
  }
</style>



