<template>
  <div class="prove-idealRecord prove-verify">
    <div class="body" :style="{backgroundImage: 'url('+note+')'}">
      <header>
          <div class="zh">志 愿 服 务 记 录 证 明</div>
          <div class="en">CERTIFICATE OF VOLUNTARY SERVICE</div>
          <div class="number">编号(No.)：AABBCCDDDEEEEEEEEEYYYYMMDD</div>
        </header>
        <div class="line"></div>
        <section class="base">
          <div class="infoTip mb-10">公益人信息（Information of charitarian）</div>
          <div class="head">
            <div class="t">姓名/Name</div>
            <div class="b mb-10">时代在变,时间在变,不变的是我们的关怀</div>
            <div class="t">公益护照号/C&V Passport No.</div>
            <div class="b mb-10">AABBCCDDDEEEEEEEEE</div>
            <div class="t">证件类型/Type of ID</div>
            <div class="b mb-10">身份证</div>
            <div class="t">证件号码/ID No.</div>
            <div class="b">310291029381728394</div>
          </div>
          <div class="line"></div>
          <div class="t">公益次数/捐赠（Charity/Donation  Times）</div>
          <div class="b">10小时</div>
          <div class="line"></div>
          <div class="t">公益内容（Charity Service Content）</div>
          <div class="button">点击查看</div>
          <div class="b"></div>
          <div class="line"></div>
          <div class="t">其他需要说明的事项（Other Information）</div>
          <div class="n">无</div>
          <div class="line"></div>
          <div class="t">是否有附件（With Attachment or Not?）</div>
          <div class="b mb-10 select row">
              <div class="l col-sm col-sm-6">
                <div @click="isClick=true">
                  <span class="radio">
                    <i class="iconfont icon-tick" v-if="isClick"></i>
                  </span>
                  <span class="radio-text">是/Yes</span>
                </div>
              </div>
              <div class="r col-sm col-sm-6">
                <div @click="isClick=false">
                  <span class="radio">
                    <i class="iconfont icon-tick" v-if="!isClick"></i>
                  </span>
                  <span class="radio-text">否/No</span>
                </div>
              </div>
          </div>
          <div class="t">签发单位(Signed by)：</div>
          <div class="b"></div>
          <div class="b time">2017年06月19日</div>
        </section>
    </div>
    <div class="tip">注:公益上海官方网站为http://www.shcvs.cn</div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            isClick: true,
            note: require('@/assets/images/idealRecord-bg.png')
        };
    },
    created () {
    },
    methods: {
    }
};
</script>



