<template>
  <div class="prove-publicity">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :bottomPullText='bottomText' :auto-fill="false" ref="loadmore">
        <header :style="{backgroundImage: 'url('+note+')'}">
          <div class="t">志愿服务记录证明的公示期为7天，<br>如无异议将自动生效。</div>
        </header>
        <section class="box" v-if="list.length !== 0">
            <ul>
              <li class="row" v-for="(item, index) in list" :key="index" @click="report(item.code)">
                <div class="col-sm col-sm-10 left">
                    <div class="name">申请人 : {{item.realName}}</div>
                    <div class="c">证明编号：{{item.code}}</div>
                    <div class="c">服务时长：{{item.time | initTime}}</div>
                    <div class="b">公示剩余：{{item.days}}日</div>
                </div>
                <div class="col-sm col-sm-1 right">
                  <i class="iconfont icon-arrow-right"></i>
                </div>
              </li>
            </ul>
        </section>
      </mt-loadmore>
      <div v-if="list.length === 0" class="error-wrap">
        <img src="../assets/images/license-error.png" alt="">
        <div class="msg">暂无数据</div>
      </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/publicity-header.png'),
            allLoaded: false,
            bottomText: '上拉加载更多...',
            form: {
                pageNumber: 1,
                pageSize: 10
            },
            list: []
        };
    },
    created () {
        this.getList();
    },
    methods: {
        getList () {
            this.$http.post(this.$api.prove.queryPublicityCertificate, this.form).then((res) => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        },
        // 跳转至 举报列表(服务记录)多选页
        report (code) {
            this.$router.push({name: 'serviceLog', query: {code: code}});
        },
        loadBottom: function () {
            if (this.list.length < 10) {
                this.$refs.loadmore.onBottomLoaded();
                this.allLoaded = true;
                return false;
            }
            this.form.pageNumber += 1;
            setTimeout(() => {
                this.$http.post(this.$api.prove.queryPublicityCertificate, this.form).then(res => {
                    if (res.data.code === 0) {
                        this.list = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                        if (res.data.data.records.length === 0) {
                            this.allLoaded = true;
                        }
                    }
                });
            }, 500);
        }
    }
};
</script>
<style lang="scss" scoped>
.prove-publicity {
  header {
    text-align: center;
    .t {
      width: 100%;
      color: #fff;
      font-size: 20px;
      font-weight: 700;
      padding-top: px2rem(40);
    }
  }
  .box {
    margin-top: px2rem(-40);
  }
  .error-wrap {
    padding: px2rem(40) 0;
    text-align: center;
    img {
      width: px2rem(180);
      height: px2rem(156);
    }
    .msg {
      color: #999999;
      margin-top: px2rem(25);
    }
  }
}
</style>



