<template>
  <div class="prove-detailedList prove-community prove-serviceLog">
    <ul v-if="list.length !== 0">
      <li class="row" v-for="(item, index) in list" :key="index">
          <div class="col-sm col-sm-5 l" @click="open(item)">
            <div>{{item.startTime | initDate}}</div>
          </div>
          <div class="col-sm col-sm-6 r" @click="open(item)">
            <div class="t">
              <i class="iconfont icon-clock-blod"></i>
              <span>{{item.times | initTime}}</span>
            </div>
            <div class="c">
              <i class="iconfont icon-person-blod"></i>
              <span>{{item.name}}</span>
            </div>
            <div class="b">
              <i class="iconfont icon-address-blod"></i>
              <span>{{item.orgName}}</span>
            </div>
          </div>
          <div class="col-sm col-sm-1 r-r">
            <div class="radio-box" @click="radio(item)">
              <span class="radio" v-show="item.check">
                <i class="iconfont icon-tick"></i>
              </span>
            </div>
          </div>
      </li>
    </ul>
    <div v-if="list.length !== 0" class="button-wrap row">
      <button class="col-sm col-sm-8" @click="submit">举 报</button>
      <button class="col-sm col-sm-3" @click="all" :class="{'allBtn': checkAll}">全 选</button>
    </div>
    <div v-else class="error-wrap">
      <img src="../assets/images/license-error.png" alt="">
      <div class="msg">暂无数据</div>
    </div>
      <div v-if="isMask">
        <div class="mask"></div>
        <div class="model">
          <div class="t row" :style="{backgroundImage: 'url('+note+')'}">
            <div class="close" @click="isMask = false">
              <i class="iconfont icon-close"></i>
            </div>
            <div class="text">
              <div class="name">公益上海</div>
              <div class="number">岗位编号：{{openForm.orgCode}}</div>
            </div>
          </div>
          <div class="b">
            <div>服务日期：{{openForm.startTime | initDate}}</div>
            <div>服务时间：{{openForm.startTime | initHours}} - {{openForm.endTime | initHours}}</div>
            <div>服务地点：{{openForm.arriveAddress}}</div>
            <!-- <div>服务内容：{{openForm.info}}</div> -->
            <div class="row">
                <div class="col-sm col-sm-6">
                  <i class="iconfont icon-sign"></i>
                  <span>签到时间</span>
                </div>
            </div>
            <div class="row">
                <div class="col-sm col-sm-6l">
                  {{openForm.arriveTime}}
                </div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/prove-head.png'),
            checkAll: false,
            isShow: false,
            isMask: false,
            list: [],
            openForm: {},
            optionsData: [
                {
                    value: 'valueF',
                    disabled: true
                },
                {
                    value: 'valueE',
                    disabled: true
                }
            ],
            code: null
        };
    },
    created () {
        this.init();
    },
    methods: {
        init () {
            this.code = this.$route.query.code;
            this.$http.post(this.$api.prove.queryServiceRecordByCode, {code: this.code}).then((res) => {
                if (res.data.code === 0) {
                    res.data.data.userTicketValidSignRespList.forEach(e => {
                        e['check'] = false;
                    });
                    this.list = res.data.data.userTicketValidSignRespList;
                }
            });
        },
        open (item) {
            this.isMask = true;
            this.openForm = item;
        },
        submit () {
            let params = {
                code: this.code,
                list: []
            };
            this.list.forEach(e => {
                if (e.check) {
                    params.list.push({serialNumber: e.serialNumber});
                }
            });
            // 举报页面跳转 返回值url如果有值 跳至返回的url 反之 跳至举报表单
            this.$http.post(this.$api.prove.reportRedirect).then((res) => {
                if (res.data.data.url) {
                    window.location.href = 'http://volunteer.shmzj.gov.cn/public-welfare/complaint';
                } else {
                    this.$router.push({name: 'report', query: params});
                }
            });
        },
        all () {
            this.checkAll = !this.checkAll;
            this.checkAll === true ? this.for(true) : this.for(false);
        },
        radio (item) {
            item.check = !item.check;
            for (let e of this.list) {
                if (!e.check) {
                    this.checkAll = false;
                    return;
                } else {
                    this.checkAll = true;
                }
            }
        },
        for (type) {
            this.list.forEach(e => {
                e.check = type;
            });
        }
    }
};
</script>
<style lang="scss" scoped>
  .prove-serviceLog {
    ul {
      padding-top: px2rem(15);
    }
    li:nth-child(1) {
      margin-top: 0;
    }
    .button-wrap {
      display: flex;
      position: fixed;
      bottom: px2rem(20);
      background: #fff;
      left: 50%;
      width: 100%;
      bottom: 0;
      transform: translateX(-50%);
      button {
        height: px2rem(40);
        font-size: 16px;
        border: none;
        color: #fff;
        border-radius: 25px;
        border-radius: 0;
        padding: 0;
        background-color: $color-highlight;
      }
      button:nth-child(1) {
        border-bottom-left-radius: 5px;
      }
      button:nth-last-child(1) {
        background-color: #f2f2f2;
        color: #999;
        border-bottom-right-radius: 5px;
        margin-left: 1px;
      }
    }
    .allBtn {
      color: #fff !important;
      background-color: #72b81e !important;
    }
    li {
      .r-r {
        text-align: right;
        .radio-box {
          width: 20px;
          height: 20px;
          line-height: 20px;
          display: block;
          border-radius: 3px;
          border: 1px solid #ccc!important;
          color: #72b81e;
          .radio {
            display: inline-block;
          }
        }
      }
    }
  }
  .prove-community {
    background-color: #fff;
    ul li {
      background-color: #f5ffe8;
    }
  }
</style>



