<template>
  <div class="prove-idealRecord">
      <div class="body" :style="{backgroundImage: 'url('+note+')'}">
        <header>
          <div class="zh">{{form.title.split("").join(" ")}}</div>
          <div class="en">CERTIFICATE OF VOLUNTARY SERVICE</div>
          <div class="number">编号(No.)：{{form.code}}</div>
        </header>
        <div class="line"></div>
        <section class="base">
          <div class="infoTip mb-10">公益人信息（Information of charitarian）</div>
          <div class="head">
            <div class="t">姓名/Name</div>
            <div class="b mb-10">{{form.realName}}</div>
            <div class="t">公益护照号/C&V Passport No.</div>
            <div class="b mb-10">{{form.acceptCode}}</div>
            <div class="t">证件类型/Type of ID</div>
            <div class="b mb-10">{{form.cartType}}</div>
            <div class="t">证件号码/ID No.</div>
            <div class="b">{{form.cartNO}}</div>
          </div>
          <div class="line"></div>
          <div class="t">公益次数/捐赠（Charity/Donation  Times）</div>
          <div class="b">{{form.time}}次</div>
          <div class="line"></div>
          <div class="t">公益内容（Charity Service Content）</div>
          <div class="button" @click="look(form)">点击查看</div>
          <div class="b"></div>
          <div class="line"></div>
          <div class="t">其他需要说明的事项（Other Information）</div>
          <div class="n">{{form.info || '无'}}</div>
          <div class="line"></div>
          <div class="t">是否有附件（With Attachment or Not?）</div>
          <div class="b mb-10 select row">
              <div class="l col-sm col-sm-6">
                <div @click="isClick=true">
                  <span class="radio">
                    <i class="iconfont icon-tick" v-if="form.isAnnex === '1'"></i>
                  </span>
                  <span class="radio-text">是/Yes</span>
                </div>
              </div>
              <div class="r col-sm col-sm-6">
                <div @click="isClick=false">
                  <span class="radio">
                    <i class="iconfont icon-tick" v-if="form.isAnnex === '0'"></i>
                  </span>
                  <span class="radio-text">否/No</span>
                </div>
              </div>
          </div>
          <div class="t">签发单位(Signed by)：</div>
          <div class="b">{{form.issued}}</div>
          <div class="b time">{{form.createTime | date}}</div>
          <div class="tip">注: <a :href="form.urlIssued">{{form.demo}}</a></div>
        </section>
      </div>
      <div class="button-w">
        <button><a :href="form.downloadUrl" :download="form.title">下载PDF</a></button>
      </div>
  </div>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/idealRecord-bg.png'),
            form: {}
        };
    },
    created () {
        this.form = this.$route.query;
    },
    methods: {
        look (item) {
            let params = {
                startDay: this.form.startDay,
                endDay: this.form.endDay,
                code: this.form.code,
                time: this.form.time
            };
            this.$router.push({name: 'charityList', query: params});
        }
    }
};
</script>



