<template>
  <div class="proveApplyFor">
    <section class="list">
        <ul>
            <li v-for="(item, index) in list" :key="index">
                <div class="item row" @click="pull(item)">
                    <div class="col-sm col-sm-2 l row">
                        <img :src="item.logo" alt="">
                    </div>
                    <div class="col-sm col-sm-9 l row">
                        <div class="info">
                            <div class="name">{{item.title}}</div>
                            <div class="rela">签发单位 : {{item.issued}}</div>
                        </div>
                    </div>
                    <div class="col-sm col-sm-1 r">
                        <i class="iconfont icon-arrow-right"></i>
                    </div>
                </div>
                <div class="line"></div>
            </li>
        </ul>
    </section>
  </div>
</template>

<script>
export default {
    data () {
        return {
            list: [],
            pages: {
                pageNumber: 1,
                pageSize: 20
            }
        };
    },
    created () {
        this.getList();
    },
    methods: {
        // 跳转
        pull (item) {
            console.log(item.type);
            // 志愿服务证明
            if (item.type === 10) {
                this.$router.push({name: 'serviceVolun', query: {certificateCode: item.certificateCode}});
            }
            // 慈善证明
            if (item.type === 20) {
                this.$router.push({name: 'error'});
                // this.$router.push({name: 'applyCertificate', query: {certificateCode: item.certificateCode}});
            }
        },
        getList () {
            this.$http.post(this.$api.prove.queryCertificateList, this.pages).then((res) => {
                if (res.data.code === 0) {
                    this.list = res.data.data.records;
                }
            });
        }
    }
};
</script>



