<template>
  <div class="prove-detailedList prove-duration">

      <header>
        <div class="t">
          <div class="title period">服务时段/Service Period</div>
          <div>{{query.beginDay1}} 至 {{query.endDay1}}</div>
        </div>
        <div class="b">
          <div class="title time">服务时长/Service Time</div>
          <div>{{form.realTimes}}</div>
        </div>
        <div class="m-b">
            <div class="">对服务记录有异议请返回去个人服务记录申诉</div>
        </div>
        <img src="../assets/images/detailed.png" alt="">
      </header>
      <ul>
          <li class="row" v-for="(item, index) in form.userTicketValidSignRespList" :key="index" @click="open(item)">
              <div class="col-sm col-sm-5 l">
                <div>{{item.startTime | initDate}}</div>
              </div>
              <div class="col-sm col-sm-7 r">
                <div class="row t">
                  <div class="col-sm col-sm-6 left">
                    <i class="iconfont icon-clock-blod"></i>
                    <span>{{initTime(item.times)}}</span>
                  </div>
                  <div class="col-sm col-sm-6 right isManual-class">
                    <span>{{item.txtManual}}</span>
                  </div>
                </div>
                <div class="c">
                  <i class="iconfont icon-person-blod"></i>
                  <span>{{item.name}}</span>
                </div>
                <div class="b">
                  <i class="iconfont icon-address-blod"></i>
                  <span>{{item.orgName}}</span>
                </div>
              </div>
          </li>
        </ul>
      <div v-if="model1">
        <div class="mask"></div>
        <div class="model">
          <div class="t row" :style="{backgroundImage: 'url('+note+')'}">
            <div class="close" @click="model1 = false">
              <i class="iconfont icon-close"></i>
            </div>
            <div class="text">
              <div class="name">活动：{{openForm.title}}</div>
              <div class="number">岗位：{{openForm.name}}</div>
            </div>
          </div>
          <div class="b">
            <div>服务日期：{{openForm.startTime | initDate }}</div>
            <div>服务时间：{{openForm.startTime | initHours }} - {{openForm.endTime | initHours}}</div>
            <div>服务地点：{{openForm.arriveAddress}}</div>
            <div class="time-wrap">
              <div v-for="(item, index) in openForm.validSignSlot" :key="index">
                <div class="row">
                    <div class="col-sm col-sm-6">
                      <i class="iconfont icon-sign"></i>
                      <span>签到时间</span>
                    </div>
                    <div class="col-sm col-sm-6">
                      <i class="iconfont icon-sign"></i>
                      <span>签出时间</span>
                    </div>
                </div>
                <div class="row">
                    <div class="col-sm col-sm-6 l">
                      {{item.startTime | initHours}}
                    </div>
                    <div class="col-sm col-sm-6 r">
                      {{item.endTime | initHours}}
                    </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="model2">
        <div class="mask"></div>
        <div class="model model-1 model-new">
          <div class="t">
            <img src="../assets/images/accomplish-1.png" alt="">
          </div>
          <div class="c">证明已提交</div>
          <div class="b">证明已提交，将进行公示，公示期为7天，公示期内不可重复提交申请，公示期内如有人举报，将进行人工审核，如果审核没有问题将继续发证明，如举报属实，证书申请将会失败。</div>
          <div class="btn-wrap-1 row">
            <button class="col-sm col-sm-4 l" @click="model2 = false">返回</button>
            <button class="col-sm col-sm-5 r" @click="lookGS">去公示页查看</button>
          </div>
        </div>
      </div>
      <section class="button-box">
          <button @click="submit">提交</button>
      </section>
  </div>
</template>

<script>
export default {
    data () {
        return {
            note: require('@/assets/images/prove-head.png'),
            isRealTimes: null,
            allLoaded: false,
            bottomText: '上拉加载更多...',
            model1: false,
            model2: false,
            form: {},
            query: {},
            openForm: {}
        };
    },
    created () {
        this.query = this.$route.query;
        this.getList();
    },
    methods: {
        getList () {
            this.$http.post(this.$api.prove.queryServiceRecord, this.query).then((res) => {
                if (res.data.code === 0) {
                    this.form = res.data.data;
                    this.query['time'] = this.form.realTimes;
                    this.form.realTimes = this.initTime(this.form.realTimes);
                    this.isRealTimes = false;
                }
            });
        },
        loadBottom: function () {
            this.form.pageNumber += 1;
            if (this.form.pageNumber === this.total) {
                this.allLoaded = true;
            }
            setTimeout(() => {
                this.$http.post(this.$api.prove.queryServiceRecord, this.query).then(res => {
                    if (res.data.code === 0) {
                        this.form.userTicketValidSignRespList = this.list.concat(res.data.data.records);
                        this.$refs.loadmore.onBottomLoaded();
                    }
                });
            }, 500);
        },
        open (item) {
            this.model1 = true;
            this.openForm = item;
        },
        submit () {
            let params = {
                personFirstAccount: this.query.user,
                certificateCode: this.query.certificateCode,
                startDay: this.query.beginDay,
                endDay: this.query.endDay,
                time: this.query.time
            };
            this.$http.post(this.$api.prove.applyCertificate, params).then((res) => {
                if (res.data.code === 0) {
                    this.model2 = true;
                    return;
                }
                this.$mint.Toast({
                    message: res.data.msg,
                    position: 'top',
                    duration: 1000
                });
            });
        },
        lookGS () {
            this.$router.push({name: 'publicity'});
        },
        initTime (v) {
            if (v < 60) {
                return v + '分钟';
            }
            return Math.floor(v / 60) + '小时' + (v % 60 === 0 ? '' : v % 60 + '分钟');
        }
    }
};
</script>
<style lang="scss" scoped>
  .prove-duration {
    .button-box {
      padding: px2rem(20) px2rem(15);
      bottom: px2rem(0);
      background-color: #fff;
    }
    .isManual-class {
      // float: right;
      text-align: right;
    }
    .model-new {
      .b, .btn-wrap-1 {
        padding: 0 px2rem(20);
      }
      .btn-wrap-1 {
        button {
          border-radius: 3px;
        }
      }
    }
  }
  .time-wrap {
    max-height: 100px;
    overflow-y: auto;
  }
</style>



